@use '../base/variables' as v;
@use '../base/mixins' as m;


.nuestros-testimoniales{
    background-color: v.$blanco;
    padding: 10rem 2rem;
    @include m.tablet { 
        padding: 10rem;
    }

    .testimoniales{
        background-color: v.$cafeClaro;
        max-width: 50rem;
        margin: 0 auto;
        padding:  2rem 4rem;
        border-radius: 1rem;
        
        .autor{
            color: v.$primario;
            font-size: 2.4rem;
            text-align: right;
            font-family: v.$fuenteCursiva;
        }
    }
}

